body{
  background-color: #052b57;
}
h1, h2, h3, h4, .navbar-brand, .nav-link{
  color: #FFFFFF!important;
}
i{
  color: #d6645a;
  font-size: 30px;
}
.row{
  padding: 5px!important;
}
.btn, .btn-outline-success{
  color: #FFFFFF!important;
  border-color: #d6645a;
  background-color: #d6645a;
}

.card{
  background-color: #A9A9A9;
}

.bg-light{
  background-color: #052b57!important;
}

@media (max-width: 767px) {
  .your-image-class {
    width: 50px;
    height: 50px;
  }
  .your-contact-card{
    height: 70px;
  }
  i{
    font-size: 15px;
  }
}

/* For larger screens */
@media (min-width: 768px) {
  .your-image-class {
    width: 100px;
    height: 100px;
  }
  .your-contact-card{
    height: 120px;
  }
  i{
    font-size: 30px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
